import React, { Component } from 'react'
import { connect } from 'react-redux'
import { createMuiTheme } from '@material-ui/core/styles'
import { ThemeProvider } from '@material-ui/styles'
import PropTypes from 'prop-types'
import RegisterContainer from '../../containers/Register'
import HeaderMobile from './HeaderMobile'
import HeaderDesktop from './HeaderDesktop'
import ACTION from '../../redux/actions/headerAction'
import headerSelector from '../../redux/selector/headerSelector'
import { themeProperty } from '../../commons/theme'

const darkTheme = createMuiTheme({
  ...themeProperty,
  palette: {
    type: 'dark',
  },
})

const MENU_LIST = [
  {
    title: 'หน้าแรก',
    to: '/',
  },
  {
    title: 'เกี่ยวกับเรา',
    to: '/about',
  },
  {
    title: 'ทำเนียบรุ่น',
    to: '/yearbook',
  },
  {
    title: 'ติดต่อเรา',
    to: '/contact',
  },
]

const APPBAR_CHANGE_HEIGHT = 50

class Header extends Component {
  state = {
    showRegister: false,
    appBarChangeColor: false,
  }

  static propTypes = {
    getImage: PropTypes.func.isRequired,
    logoDesktop: PropTypes.string.isRequired,
    logoMobile: PropTypes.string.isRequired,
    forceChange: PropTypes.bool,
  }

  static defaultProps = {
    forceChange: false,
  }

  componentDidMount() {
    const { logoDesktop, logoMobile, getImage } = this.props
    if (!logoDesktop || !logoMobile) {
      getImage()
    }
    window.addEventListener('scroll', this.handleScroll)
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll = () => {
    const { appBarChangeColor } = this.state
    if (window.scrollY >= APPBAR_CHANGE_HEIGHT && !appBarChangeColor) {
      this.setState({ appBarChangeColor: true })
    }
    if (window.scrollY < APPBAR_CHANGE_HEIGHT && appBarChangeColor) {
      this.setState({ appBarChangeColor: false })
    }
  }

  onOpenRegister = () => this.setState({ showRegister: true })

  onCloseRegister = () => this.setState({ showRegister: false })

  handleClickRegister = () => this.onOpenRegister()

  render() {
    const { showRegister, appBarChangeColor } = this.state
    const { logoDesktop, logoMobile, forceChange } = this.props
    return (
      <>
        <ThemeProvider theme={darkTheme}>
          <HeaderDesktop
            changeColor={forceChange || appBarChangeColor}
            onClickRegister={this.handleClickRegister}
            logo={logoDesktop}
            menu={MENU_LIST}
          />
          <HeaderMobile
            changeColor={forceChange || appBarChangeColor}
            onClickRegister={this.handleClickRegister}
            logo={logoMobile}
            menu={MENU_LIST}
          />
        </ThemeProvider>
        <RegisterContainer visible={showRegister} handleClose={this.onCloseRegister} />
      </>
    )
  }
}

const mapStateToProps = state => {
  return headerSelector(state)
}

const mapDispatchToProps = {
  getImage: ACTION.getImage,
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Header)
