import _reduce from 'lodash/reduce'
import _toString from 'lodash/toString'
import _some from 'lodash/some'
import _reverse from 'lodash/reverse'
import _sortBy from 'lodash/sortBy'
import _toNumber from 'lodash/toNumber'
import ACTION from '../actionTypes/yearbookActionTypes'
import { urlToKey, getSheet } from 'sheetsy'
import { SHEET_KEY } from '../../commons/constant'

const mapYearBookData = item => ({
  facebook: item.facebook,
  instagram: item.instagram,
  imageUrl: item.imageurl,
  major: item.major,
  name: item.name,
  nickname: item.nickname,
  studentID: item.studentid,
})

const getYearbookDetailRequest = () => ({
  type: ACTION.GET_YEARBOOK_DETAILS.REQUEST,
})

const getYearbookDetailError = error => ({
  type: ACTION.GET_YEARBOOK_DETAILS.FAILURE,
  payload: error,
})

const setContent = payload => ({
  type: ACTION.GET_YEARBOOK_DETAILS.SUCCESS,
  payload: payload,
})

const getContent = () => async dispatch => {
  dispatch(getYearbookDetailRequest())
  try {
    const yearbookSheet = await getSheet(urlToKey(SHEET_KEY), 'oqvy65n')
    const rowData = yearbookSheet.rows
    const generation = []
    const list = _reduce(
      rowData,
      (acc, current) => {
        const mappedItem = mapYearBookData(current)
        const gen = _toString(mappedItem.studentID.substring(0, 2))
        if (_some(Object.keys(acc), item => item === gen)) {
          const currentAcc = { ...acc }
          currentAcc[gen].push(mappedItem)
          return currentAcc
        } else {
          generation.push(gen)
          return {
            ...acc,
            [gen]: [mappedItem],
          }
        }
      },
      {}
    )
    dispatch(setContent({ generation: _reverse(_sortBy(generation, gen => _toNumber(gen))), list }))
  } catch (error) {
    dispatch(getYearbookDetailError(error))
  }
}

export default {
  getContent,
  setContent,
}
