import initialState from './initialState'
import ACTION from '../../actionTypes/yearbookActionTypes'

const yearbookReducer = (state = initialState, action) => {
  switch (action.type) {
    case ACTION.GET_YEARBOOK_DETAILS.REQUEST:
      return {
        ...state,
        isFetching: true,
      }
    case ACTION.GET_YEARBOOK_DETAILS.SUCCESS:
      return {
        ...state,
        ...action.payload,
        isFetching: false,
      }
    case ACTION.GET_YEARBOOK_DETAILS.FAILURE:
      return {
        ...state,
        error: action.payload,
        isFetching: false,
      }
    default:
      return state
  }
}

export default yearbookReducer
