import React, { Component } from 'react'
import { connect } from 'react-redux'
import {
  Typography,
  Container,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  LinearProgress,
  Card,
  Fab,
} from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import Icon from '@material-ui/core/Icon'
import { Responsive } from 'semantic-ui-react'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import yearbookSelector from '../../redux/selector/yearbookSelector'
import ACTION from '../../redux/actions/yearbookAction'
import styles from './styles'

export class YearbookPage extends Component {
  static propTypes = {
    generation: PropTypes.array.isRequired,
    list: PropTypes.object.isRequired,
    getContent: PropTypes.func.isRequired,
    classes: PropTypes.object.isRequired,
    isFetching: PropTypes.bool.isRequired,
  }

  state = {
    selectedGen: '',
  }

  onChangeGen = event => this.setState({ selectedGen: event.target.value })

  componentDidMount() {
    this.props.getContent()
  }

  renderProfile = item => {
    const { classes } = this.props
    console.log('item', item)
    return (
      <Card key={item.name} className={classes.profileContainer}>
        <img
          src={item.imageUrl}
          alt={item.name}
          width={700}
          height={700}
          className={classes.profileImage}
        />

        <Typography variant="h5" align="center" color="secondary">
          {item.name}
        </Typography>
        <Container>
          <div className={classes.detailLine}>
            <Typography>ชื่อเล่น</Typography> <Typography>{item.nickname}</Typography>
          </div>
          <div className={classes.detailLine}>
            <Typography>รหัสนักศึกษา</Typography> <Typography>{item.studentID}</Typography>
          </div>
          <div className={classes.detailLine}>
            <Typography>สาขาวิชา</Typography> <Typography>{item.major}</Typography>
          </div>
        </Container>
        <div className={classes.iconContainer}>
          <Fab
            className={classes.profileSocial}
            color="primary"
            href={item.facebook}
            target="_blank"
          >
            <Icon
              className={clsx(classes.profileSocialIcon, 'fab fa-facebook-square')}
              fontSize="large"
            />
          </Fab>
          <Fab
            className={classes.profileSocial}
            color="secondary"
            href={item.instagram}
            target="_blank"
          >
            <Icon
              className={clsx(classes.profileSocialIcon, 'fab fa-instagram')}
              fontSize="large"
            />
          </Fab>
        </div>
      </Card>
    )
  }

  render() {
    const { classes, generation, list, isFetching } = this.props
    const { selectedGen } = this.state
    const currentSelect = selectedGen || generation[0] || ''
    return (
      <Container className={classes.page}>
        {isFetching ? (
          <LinearProgress color="secondary" />
        ) : (
          <>
            <FormControl className={classes.formControl}>
              <InputLabel htmlFor="age-simple">รุ่นที่</InputLabel>
              <Select
                value={currentSelect}
                onChange={this.onChangeGen}
                inputProps={{
                  name: 'age',
                  id: 'age-simple',
                }}
              >
                {generation.map(item => (
                  <MenuItem key={item} value={item}>
                    รุ่นที่ {item - 48}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <div className={classes.listContainer}>
              <Typography variant="h5">
                ผู้นำเชียร์แห่งวิทยาลัยศิลปะ สื่อ และเทคโนโลยี รุ่นที่ {Number(currentSelect) - 48}
              </Typography>
              <div className={classes.cardContainer}>
                {currentSelect && list[currentSelect].map(item => this.renderProfile(item))}
              </div>
            </div>
          </>
        )}
      </Container>
    )
  }
}

const mapStateToProps = state => yearbookSelector(state)

const mapDispatchToProps = {
  getContent: ACTION.getContent,
}

const styleConnector = withStyles(styles)(YearbookPage)

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(styleConnector)
